.grid-container {
  width: 100vw;
  max-width: 500px;
  .items-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    @media (max-width: 395px) {
      width: 100%;
    }
    .item {
      width: 102px;
      height: 78px;
      box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.175);
      margin: 8px;
      cursor: pointer;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: background-color 200ms;
      > span {
        width: 100%;
        height: 100%;
      }
      &.active {
        box-shadow: 0 0 0 6px rgba(51, 156, 32, 0.8);
      }
      &.selected {
        box-shadow: 0 0 0 6px rgb(26 127 139);
      }
      .multi-object-wrapper {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        > div {
          width: 46%;
          height: 100%;
          background-size: cover;
        }
      }
    }
  }
  &.standard .item:hover {
    background-color: transparent;
    cursor: unset;
  }
}